<template>
  <form @submit.prevent="submit" class="form">
    <input
      :title="nameTitle"
      :placeholder="namePlaceholder"
      v-model.lazy.trim="name"
      type="text"
      class="form__name"
      pattern=".{2,40}"
      maxlength="40"
      spellcheck="false"
      required
    />

    <textarea
      :title="textAreaTitle"
      :placeholder="textAreaPlaceholder"
      :minlength="min"
      :maxlength="max"
      v-model.trim="comment"
      cols="50"
      rows="4"
      class="textarea__comment"
      required
    ></textarea>

    <CommentLength :max="max" :comment="comment" />
    <CommentPreview :name="name" :comment="comment" />
    <Button type="submit">–û–ø—É–±–ª–∏–∫–æ–≤–∞—Ç—å</Button>
  </form>
</template>

<script>
import { mapMutations, mapActions } from "vuex";
import { createToast } from "mosha-vue-toastify";

import CommentLength from "./CommentLength.vue";
import CommentPreview from "./CommentPreview.vue";
import Button from "./Button.vue";

export default {
  name: "Form",
  components: {
    CommentLength,
    CommentPreview,
    Button,
  },
  data() {
    return {
      name: "",
      comment: "",
      min: 2,
      max: 200,
      nameTitle: "–ò–º—è –º–æ–∂–µ—Ç –±—ã—Ç—å –æ—Ç 2 –¥–æ 40 —Å–∏–º–≤–æ–ª–æ–≤",
      namePlaceholder: "–í–∞—à–µ –∏–º—è...",
      textAreaTitle: "–î–ª–∏–Ω–Ω–∞ —Å–æ–æ–±—â–µ–Ω–∏—è –æ—Ç 2 –¥–æ 200 —Å–∏–º–≤–æ–ª–æ–≤",
      textAreaPlaceholder: "–í–≤–µ–¥–∏—Ç–µ —Ç–µ–∫—Å—Ç —Å–æ–æ–±—â–µ–Ω–∏—è...",
    };
  },
  methods: {
    ...mapMutations(["createPost"]),
    ...mapActions(["setPosts"]),
    submit() {
      if (this.name === "" || this.comment === "") return;

      const newPost = {
        id: new Date().valueOf(),
        name: this.name,
        comment: this.comment,
        date: new Date()
          .toISOString()
          .split("T")[0]
          .split("-")
          .reverse()
          .join("."),
        time: new Date().toString().split(" ")[4],
      };

      this.createPost(newPost);
      this.setPosts();

      createToast("–ó–∞–ø–∏—Å—å —É—Å–ø–µ—à–Ω–æ –¥–æ–±–∞–≤–ª–µ–Ω–∞", {
        showIcon: true,
        timeout: 2000,
        position: "top-center",
        type: "success",
        transition: "zoom",
      });

      this.reset();
    },
    reset() {
      this.name = "";
      this.comment = "";
    },
  },
};
</script>

<style scoped>
.form {
  position: relative;
  margin: 0 auto;
  padding-top: 20px;
  max-width: 400px;

  text-align: center;
}

.form__name {
  margin-bottom: 15px;
  border: 2px solid rgb(44, 62, 80, 0.3);
  border-radius: 10px;
  padding: 5px;

  font: inherit;
}

.form__name:focus:invalid {
  outline: 2px solid #f73d3d;
}

.form__name:focus:valid {
  outline: 2px solid #40ae00;
}

.textarea__comment {
  max-width: 100%;
  border: 2px solid rgb(44, 62, 80, 0.3);
  border-radius: 10px;
  padding: 15px;
  resize: vertical;
}
</style>